import React from 'react';
import ReactDOM from 'react-dom';
import i18next from 'i18next';
import AlertBarIconConfig from '../components/config/AlertBarIconConfig';

class AlertBarIconConfigElement extends HTMLElement {
  constructor() {
    super();
    this.newConfig = {};
    this.container = null;
    this.reactRootRef = React.createRef();
  }

  get config() {
    const config = this.reactRootRef.current ? this.reactRootRef.current.state : {};

    Object.keys(config).forEach(key => {
      config[key] = typeof config[key] === 'string' ? config[key] : JSON.stringify(config[key]);
    });
    return config;
  }

  set config(value) {
    this.newConfig = value;
    this.render();
  }

  connectedCallback() {
    this.container = document.createElement('div');
    this.appendChild(this.container);

    this.render();
  }

  render() {
    const locale = this.getAttribute('locale') || 'en';
    i18next.changeLanguage(locale);

    ReactDOM.render(
      <AlertBarIconConfig
        ref={this.reactRootRef}
        config={Object.keys(this.newConfig) ? this.newConfig : this.config}
      />,
      this.container
    );
  }
}

if (!customElements.get('sd-alert-bar-icon-config')) {
  customElements.define('sd-alert-bar-icon-config', AlertBarIconConfigElement);
}
